function MainPage()
{
  const [open,setOpen] = React.useState(false);
  const [value, setValue] = React.useState('1');
  const handleChange = (event, newValue) => 
  {
    setValue(newValue);
  };
  const openDrawer = () =>
  {
    setOpen(true);
  }
  const closeDrawer = () =>
  {
    setOpen(false);
  }
  //Create simple drawer in const
  const drawer = (
    <Drawer anchor={"left"} open={open} onClose={closeDrawer} variant={"temporary"} PaperProps={{sx:{backgroundColor:"rgba(255,255,255,0.1)",color:'white'}}}>
      <div className="drawer">
        <List>
          <ListItem>
            <ListItemIcon>
              <HomeIcon />
            </ListItemIcon>
            <ListItemText primary="Home" />
          </ListItem>
        </List>
      </div>
    </Drawer>

  );
  return(
    <div className="mainPage">
    {drawer}
    <AppBar position="fixed" color="primary" sx={{ top: 'auto', bottom: 0,bgcolor:'rgba(255,255,255,0.1)' }}>
      <Toolbar>
        <IconButton color="inherit" aria-label="open drawer">
          <MenuIcon onClick={openDrawer} />
        </IconButton>
        <StyledFab color="primary" aria-label="add" sx={{background:'linear-gradient(0deg,#225ca24d,rgba(34, 92, 162, 0.8))'}}>
          <AddIcon />
        </StyledFab>
        <Box sx={{ flexGrow: 1 }} />
        <IconButton color="inherit">
          <SearchIcon />
        </IconButton>
      </Toolbar>
    </AppBar>
  </div>
  )
}

